import React from 'react'

import '../assets/css/IceBlock.css'

const IceBlock = (props) => {
  const transform = props.style ? props.style.transform : ''
  const transition = props.style ? props.style.transition : ''
  const styleIceBlock = {
    top: `${props.top}px`,
    left: `${props.left}px`,
    transform: `${transform}`,
    transition: `${transition}`,
  }
  return (
    <div
      className={`ice-block ice-block-${props.id}`}
      style={styleIceBlock}
    ></div>
  )
}

export default IceBlock
